.Card {
  background-color: #333;
  height: 273px;
  width: 184px;
  position: relative;
  border: none;
  cursor: pointer;
}

.Card img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (max-width: 764px) {
  .Card {
    height: 137px;
    width: 92px;
  }
}
